@import "./variables";
@import "./mixins";

@import "./scaffolding";
@import "./base";

@import "./fonts";
@import "./slider";
@import "./tooltip";

.is-wrapper {
  position: relative;
}

.is-scene-container {
  width: 100%;
  height: 100vh;

  .swiper-wrapper {
    position: relative;

    @include mobile-max() {
      transform: none !important;
    }

    & > div {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
  }
}

.is-thumb-container {
  position: absolute;
  bottom: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  transition: transform 0.25s ease-in-out;
  will-change: transform;
  z-index: 1;

  &.is-hidden {
    transform: translateY($thumb-pad-mobile * 2 + $thumb-height-mobile);

    @include tablet() {
      transform: translateY($thumb-pad-tablet * 2 + $thumb-height-tablet);
    }

    @include laptop() {
      transform: translateY($thumb-pad-laptop * 2 + $thumb-height-laptop);
    }

    .is-btn-toggle {
      img {
        transform: rotate(0);
      }

      &:hover {
        img {
          @include laptop() {
            transform: scale(1.05);
          }
        }
      }
    }
  }

  .swiper-container {
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.thumbnails-header {
  display: flex;
  margin-left: $thumb-pad-mobile;

  @include tablet() {
    margin-left: $thumb-pad-tablet * 2;
  }

  @include laptop() {
    margin-left: $thumb-pad-laptop * 2;
  }
}

.is-btn {
  position: relative;
  z-index: 1;
  height: 30px;
  width: 50px;
  border-radius: 5px 5px 0 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;

  img {
    width: 40%;
  }

  @include tablet() {
    height: 40px;
    width: 65px;
    margin: 0 15px;

    img {
      width: 50%;
    }
  }

  @include laptop() {
    height: 50px;
    width: 85px;
    cursor: pointer;

    img {
      width: auto;
    }

    &:hover {
      img {
        transform: scale(1.05);
      }
    }

    img {
      transition: transform 0.15s ease-in-out;
    }
  }
}

.is-btn-toggle {
  img {
    transform: rotate(180deg);
  }

  @include laptop() {
    &:hover {
      img {
        transform: scale(1.05) rotate(180deg);
      }
    }
  }
}

.is-thumb-wrapper {
  display: flex;
  padding: $thumb-pad-mobile 0;
  white-space: nowrap;
  overflow: scroll;

  @include tablet() {
    padding: $thumb-pad-tablet 0;
  }

  @include laptop() {
    padding: $thumb-pad-laptop 0;
  }

  &:after {
    content: "q";
    display: block;
    width: $thumb-pad-mobile;
    opacity: 0;
    visibility: hidden;

    @include tablet() {
      width: $thumb-pad-tablet;
    }

    @include laptop() {
      width: $thumb-pad-laptop;
    }
  }

  p {
    margin: 0;
    padding: 3px 0;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 14px;
    line-height: 1.25;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.65);
    text-align: center;
  }
}

.is-thumb {
  position: relative;
  height: $thumb-height-mobile;
  min-width: $thumb-width-mobile;
  width: $thumb-width-mobile;
  margin-right: $thumb-pad-mobile;
  border: 4px solid transparent;
  transition: border-color 0.15s ease-in-out;

  @include tablet() {
    height: $thumb-height-tablet;
    min-width: $thumb-width-tablet;
    width: $thumb-width-tablet;
  }

  @include laptop() {
    height: $thumb-height-laptop;
    min-width: $thumb-width-laptop;
    width: $thumb-width-laptop;
  }

  &:first-child {
    margin-left: $thumb-pad-mobile;

    @include tablet() {
      margin-left: $thumb-pad-tablet;
    }

    @include laptop() {
      margin-left: $thumb-pad-laptop;
    }
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover,
  &.is-active {
    border-color: #b8cbc8;
  }

  @include laptop() {
    cursor: pointer;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
}
